  public davaloritem(data, campo) {
		let val = '';
    if (data[campo]) {val = data[campo]; } else {val = ''; }
		return val;
	}



	public daacciones(accion, op) {

    let j = 0;
		for (let i = 0; i < accion.length; i++) {
				 if (accion[i].idpapa === op) {


          if (j === 0) {
            this.botonesactividad.push( {
              name: accion[i].nombre,
              type: 'radio',
              label: accion[i].nombre,
              checked: true,
              value: accion[i],
            });
            j = j + 1;
          } else {
            this.botonesactividad.push( {
              name: accion[i].nombre,
              type: 'radio',
              label: accion[i].nombre,
              checked: false,
              value: accion[i],
            });
          }





				 }
		}


  }

	public dalistacat(data, op) {
		let val: any;
		for (let i = 0; i < data.length; i++) {
				 if (data[i].label === op) {

					val = {key: data[i].key, label: data[i].label};
					break;
				 }
		}

		return val;
	}

  daactividadactual2(clase, actividades): any {
    let re;
    for (let i = 0; i < actividades.length; i++) {
      if (actividades[i]._id === clase) {
        re = actividades[i];
        break;
      }
    }
    return re;
  }

daactividadactual(clase, actividades): any {
  let re;
  for (let i = 0; i < actividades.length; i++) {
    if (actividades[i].clase === clase) {
      re = actividades[i];
      break;
    }
  }
  return re;
}

daactividadsiguiente(idx, actividades): any {
  let re;
  for (let i = 0; i < actividades.length; i++) {
    if (actividades[i]._id === idx) {
      re = actividades[i];
      break;
    }
  }
  return re;
}

existecampo(vcampos, idcampo): any {
  let re = false;
  for (let i = 0; i < vcampos.length; i++) {
    if (vcampos[i] === idcampo) {
      re = true;
      break;
    }
  }
  return re;
}
